	white-space: nowrap;
	width: 100%;
}

.main-navigation .menu a,
.main-navigation .menu .fa {
	color: #385f7e;
	line-height: 2;
}

.main-navigation .menu .menu-item-has-children > a {
	padding-right: 0.25em;
}

.main-navigation .menu .menu-item-has-children > a + button {
	height: 100%;
	position: absolute;
	top: 0;
	right: 0;
	line-height: inherit;
	background-color: transparent;
	border: none;
	font-size: 125%;
	padding: 0;
	z-index: 99999;
	min-width: 82px;
}

