.main-navigation .menu .sub-menu li.focus > ul {
	left: 100%;
}

.main-navigation .menu .sub-menu.toggled {
	background-color: #EAF1F7;
	position: initial;
	float: none;
	display: block;
	z-index: initial;
	box-shadow: inset 0 1px 2px #666;
}

