/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.blog .site-content:before,
.single .site-content:before,
.post-template-single-portfolio-item .post .entry-header:before,
.entry-content:after,
