.single .site-content:after,
.post-template-single-portfolio-item .post .entry-header:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.blog .site-content:after,
.single .site-content:after,
.post-template-single-portfolio-item .post .entry-header:after,
.comment-content:after,
